export function makeHttpRequest(requestObject) {
  console.log(requestObject);
  return new Promise((resolve, reject) => {
    var xmlHttp = new XMLHttpRequest();
    xmlHttp.open(requestObject.method, requestObject.url, true);
    xmlHttp.setRequestHeader(
      "Content-Type",
      requestObject.contentType
    );
    xmlHttp.onreadystatechange = function () {
      if (xmlHttp.readyState == 4) {
        let jsonResponse = xmlHttp.getResponseHeader("Content-Type") != null && xmlHttp.getResponseHeader("Content-Type").includes("application/json")
        if (xmlHttp.status >= 200 && xmlHttp.status < 400) {
          jsonResponse ? resolve(JSON.parse(xmlHttp.responseText), xmlHttp.status, requestObject) : resolve(xmlHttp.responseText, xmlHttp.status, requestObject);
        } else {
          jsonResponse ? reject(JSON.parse(xmlHttp.responseText), xmlHttp.status, requestObject) : reject(xmlHttp.responseText, xmlHttp.status, requestObject);
        }
      }
    };
    xmlHttp.send(requestObject.params);
  });
}