<template>
  <div class="bC">
    <div class="pT">
      <img src="@/assets/logo.svg" />
      <span>Azito Accounts</span>
    </div>
    <div class="pT">
      {{ message }}
      <form method="post" @submit.prevent="submitLogin">
        <div>
          <input v-model="form.userName" type="text" name="userName" placeholder="Username" />
        </div>
        <div>
          <input v-model="form.password" type="password" name="password" placeholder="Password" />
        </div>
        <div v-if="mode==='signup'">
          <input v-model="form.displayName" type="text" name="displayName" placeholder="Display Name" />
        </div>
        <div>
          <input class="redBtn" type="submit" :value="mode==='login'?'Log In':'Create account'" />
        </div>
      </form>
      <div>
          <span v-if="mode==='login'" v-text="'Sign Up'" @click="mode='signup'"/>
          <span v-if="mode==='signup'" v-text="'Log in'" @click="mode='login'"/>
      </div>
    </div>
  </div>
</template>
<script>
  import {
    makeHttpRequest
  } from "@/utils/httpUtils";
  import {
    setTimeout
  } from "timers";
  export default {
    name: "Login",
    data() {
      return {
        form: {
          userName: null,
          password: null
        },
        mode:'login',
        message: null
      };
    },
    mounted() {
      this.initWebSocket();
      if (!this.$route.query.serurl) {
        this.$route.query.serurl = "/accounts"
      }
    },
    methods: {
      initWebSocket() {
        const url = "ws://hari:3000";
        const connection = new WebSocket(url);
        // connection.onopen = () => {
        //  console.log("websocket open")
        // };
        connection.onerror = error => {
          console.log(`WebSocket error: ${error}`);
        };
        connection.onmessage = e => {
          console.log(`WebSocket Message: ${e.data}`);
          let message=JSON.parse(e.data)
          if (message.from == "IAM" && message.code=="IAM_1") {
            window.location.href = this.$route.query.serurl;
          }
        };
      },
      submitLogin() {
        if (
          this.form.userName != null &&
          this.form.userName.length > 0 &&
          this.form.password &&
          this.form.password.length > 0
        ) {
          makeHttpRequest({
              url: "/auth/"+this.mode,
              method: "POST",
              params: "userName=" +
                this.form.userName +
                "&password=" +
                this.form.password +
                (this.$route.query.serurl ?
                  "&serurl=" + this.$route.query.serurl :
                  "")+(this.form.displayName?
                  "&displayName=" + this.form.displayName :
                  ""),
              contentType: "application/x-www-form-urlencoded;charset=UTF-8"
            })
            .then(response => {
              window.location.href = response.redirect;
            })
            .catch(this.showmsg);
        } else {
          this.showmsg({
            message: "Username and password cannot be empty"
          });
        }
      },
      showmsg(response) {
        console.log(response);
        this.message = response.message;
        setTimeout(() => {
          this.message = null;
        }, 2000);
      }
    }
  };
</script>
